            let temp = []
            for (let ele in this.selectedItemList) {
                let lineData = this.selectedItemList[ele]
                //console.log('vvv', lineData['word'])
                if (this.topicWordsCandidatesOldWords.indexOf(lineData['word']) !== -1) {
                    //console.log('')
                    continue
                } else {
                    //console.log('')
                    temp.push({
                        'id': lineData['_id']['$oid'],
                        'word': lineData['word'],
                        'baiduIndex': lineData['baiduIndex'],
                        'bidPrice': lineData['bidPrice'],
                        'searchCount': lineData['searchCount'],
                        'baiduIndexM': lineData['baiduIndexM'],
                        'bidPriceM': lineData['bidPriceM'],
                        'searchCountM': lineData['searchCountM'],
                    })
                }
            }
            //    topicWordsCandidatesOld 

            this.topicWordsCandidatesTotal = [...new Set(this.topicWordsCandidatesOld.concat(temp))]
            //console.log('topicWordsCandidatesOldWords', this.topicWordsCandidatesOldWords)
            //console.log('this.topicWordsCandidatesTotal', this.topicWordsCandidatesTotal)

        },
        createExtendedWords: function (data2Create) {
            // console.log(data2Create)
            let self = this
            self.currentPage = Math.ceil((self.itemCount + 1) / self.pageSize)
            let pageParams = {
                'currentPage': self.currentPage,
                'pageSize': self.pageSize
            }
            self.axios({
                    method: 'post',
                    url: self.baseurl + 'extendedWords/' + self.currentComponent,
                    data: data2Create,
                    params: pageParams
                })
                .then(res => {
                    // console.log(res)
                    // self.currentPage = 1
                    if (res.data.count !== '') {
                        self.itemCount = res.data.count
                    }
                    self.extendedWordsItemData = res.data.content
                    //self.formCustom = self.formCustomOrigin
                })
                .catch(err => {
                    // console.log(err.response.data.detail)
                    //self.$Message.error(err.response.data.detail);
                    self.$Message.error({
                        content: JSON.stringify(err.response.data.detail),
                        duration: 0,
                        closable: true
                    });
                    //self.formCustom = self.formCustomOrigin
                })
        },
        fetchProjectUsageTags: function () {
            // extendedWords  usage tags
            let self = this
            self.axios({
                    method: 'get',
                    url: self.baseurl + 'extendedWords/usageTag/' + self.currentComponent
                })
                .then(res => {
                    // console.log(res)
                    self.projetcUsageTagsList = [] // 
                    for (let ele in res.data.content) {
                        self.projetcUsageTagsList.push({
                            'label': res.data.content[ele],
                            'value': res.data.content[ele]
                        })
                    }
                    // console.log(self.projetcUsageTagsList)
                    self.columns1[17].filters = self.projetcUsageTagsList //  
                    // console.log(self.rawCategories)
                })
                .catch(err => {
                    console.log(err)
                    // self.$Message.error(err.response.data.detail);
                })

        },
        fetchProjectCategories: function () {
            //   
            let self = this
            self.axios({
                    method: 'get',
                    url: self.baseurl + 'Categories/' + self.currentComponent
                })
                .then(res => {
                    //console.log(res)
                    for (let ele in res.data.content) {
                        self.projetcCategoriesList.push({
                            'label': res.data.content[ele].categoryName,
                            'value': res.data.content[ele].categoryName
                        })
                        self.rawCategories.push(res.data.content[ele].categoryName)
                    }
                    // console.log('projetcCategoriesList',self.projetcCategoriesList)
                    self.columns1[4].filters = self.projetcCategoriesList //  
                    // console.log(self.rawCategories)
                })
                .catch(err => {
                    console.log(err)
                    // self.$Message.error(err.response.data.detail);
                })
        },
        fetchItemsES: function (getParams) {
            let self = this
            self.tableLoading = true
            let Params = getParams
            self.axios({
                    method: 'get',
                    url: self.baseurl + 'extendedWords/es/' + self.currentComponent,
                    params: Params
                })
                .then(res => {
                    //console.log(res)
                    // self.currentPage = 1
                    self.tableLoading = false
                    if (res.data.count !== '') {
                        self.itemCount = res.data.count
                    }
                    self.extendedWordsItemData = res.data.content
                    // console.log(self.extendedWordsItemData)
                })
                .catch(err => {
                    console.log(err)
                    // self.$Message.error(err.response.data.detail);
                })
        },
        fetchItems: function (getParams) {
            let self = this
            self.tableLoading = true
            let Params = getParams
            self.axios({
                    method: 'get',
                    url: self.baseurl + 'extendedWords/' + self.currentComponent,
                    params: Params
                })
                .then(res => {
                    //console.log(res)
